.dark-theme {
  --background: var(--dark-bg);
  --text-color: var(--dark-text2);
  --text-color2: var(--dark-text1);
  --card-bg: var(--dark-card);
  --card-hover: var(--dark-card-hover);
  --count: var(--light-bg);
  --toggle-bg-s: var(--toggle-bg-start);
  --toggle-bg-e: var(--toggle-bg-end);
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*, *::before, *::after {
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-inter);
  background-color: var(--background);
  color: var(--text-color);
}

h1, h2, h3 {
  margin-top: 0;
  line-height: 1.1;
}

h1 {
  font-size: 1.5rem;
  margin-bottom: 0.1875rem;
}
@media (min-width: 71.875em) {
  h1 {
    font-size: 1.75rem;
  }
}

h2 {
  font-size: 1.5rem;
  margin-bottom: 1.5rem;
}

a, a:visited, a:active {
  text-decoration: none;
}

.container {
  max-width: 69.375rem;
  padding: 0 1.5625rem;
  margin: 0 auto 2.875rem;
}
@media (min-width: 71.875em) {
  .container {
    padding: 0;
  }
}

.cards {
  display: grid;
  grid-template-columns: 1fr;
  gap: 1.875rem;
}
@media (min-width: 40em) {
  .cards {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 71.875em) {
  .cards {
    grid-template-columns: repeat(4, 1fr);
  }
}

.card {
  position: relative;
  overflow: hidden;
  background-color: var(--card-bg);
  color: var(--text-color2);
  padding: 1.5625rem;
  border-radius: 0.3125rem;
  text-align: center;
  transition: background 150ms ease-in-out;
}
.card:hover {
  background-color: var(--card-hover);
}
.card--facebook {
  border-top: 0.3125rem solid var(--facebook);
}
.card--twitter {
  border-top: 0.3125rem solid var(--twitter);
}
.card--instagram {
  padding-top: 1.875rem;
}
.card--instagram::before {
  content: "";
  position: absolute;
  display: block;
  left: 0;
  top: 0;
  background: linear-gradient(225deg, var(--instagram-end) 30%, var(--instagram-start) 100%);
  height: 5px;
  width: 100%;
}
.card--youtube {
  border-top: 0.3125rem solid var(--youtube);
}
.card__platform {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 0.5rem;
  margin-bottom: 1.75rem;
}
.card__platform img {
  height: 1.25rem;
}
.card__subtitle {
  font-size: 0.875rem;
  font-weight: 700;
}
.card > img {
  height: 1.25rem;
}
.card__count {
  color: var(--count);
  font-weight: 700;
  letter-spacing: -0.125rem;
  line-height: 1;
}
.card__count--big {
  font-size: 3.5rem;
}
.card__count--small {
  font-size: 2rem;
}
.card__label {
  color: var(--text-color2);
  font-size: 0.75rem;
  font-weight: 400;
  text-transform: uppercase;
  letter-spacing: 0.3125rem;
  margin-top: 0.5625rem;
  margin-bottom: 1.75rem;
}
.card__change {
  display: flex;
  font-size: 0.75rem;
  font-weight: 700;
  align-items: center;
  justify-content: center;
  gap: 0.3125rem;
}
.card__change--up {
  color: var(--limegreen);
}
.card__change--down {
  color: var(--brightred);
}
.card__change img {
  width: 0.5625rem;
}

.toggle {
  border: none;
  display: grid;
  grid-template-columns: 1fr 3rem;
  color: var(--dark-text1);
  column-gap: 0.375rem;
  width: 100%;
  border-top: 0.0625rem solid var(--dark-text1);
  margin-top: 1.25rem;
  padding-top: 1.25rem;
}
@media (min-width: 40em) {
  .toggle {
    border-top: 0rem;
    width: auto;
    margin: 0;
    padding: 0;
  }
}
.toggle__title {
  font-size: 0.875rem;
  font-weight: 700;
  align-self: center;
}
.toggle__wrapper {
  height: 1.5rem;
  position: relative;
}
.toggle__input {
  opacity: 0;
  margin: 0;
  width: 1.25rem;
  height: 1.5rem;
}
.toggle__background {
  display: block;
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  border-radius: 1.25rem;
  pointer-events: none;
  background: linear-gradient(225deg, var(--toggle-bg-e) 0%, var(--toggle-bg-s) 98.02%);
}
.toggle__button {
  position: absolute;
  left: 0.21875rem;
  top: 0.1875rem;
  width: 1.125rem;
  height: 1.125rem;
  border-radius: 50%;
  background-color: var(--background);
  transition: all 300ms ease-in-out;
}
#light:checked ~ .toggle__button {
  left: calc(100% - 1.3125rem);
  right: 0.1875rem;
}

.header {
  display: flex;
  flex-wrap: wrap;
  margin-top: 2.25rem;
}
@media (min-width: 40em) {
  .header {
    justify-content: space-between;
    align-items: center;
  }
}
.header__title {
  width: 100%;
}
@media (min-width: 40em) {
  .header__title {
    width: auto;
  }
}
.header__subtitle {
  font-size: 0.875rem;
  font-weight: 700;
  color: var(--text-color2);
}

.card-grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: 1fr 1fr;
  row-gap: 1.3125rem;
}
.card-grid .card__subtitle {
  align-self: center;
  justify-self: start;
}
.card-grid img {
  align-self: center;
  justify-self: end;
}
.card-grid .card__count {
  margin-bottom: -0.125rem;
  align-self: end;
  justify-self: start;
}
.card-grid .card__change {
  align-self: end;
  justify-self: end;
}